
body {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;

    .wrapper {
        width: 1144px;
        min-width: 1144px;
        max-width: 1144px;
    }
}

table {
    font-size: 13px;
    width: 100%;

    thead {
        color: gray;

        tr {
            td {

            }
        }
    }

    tbody {
        tr {
            td {
                vertical-align: top;
                padding: 6px;
            }

            &:nth-child(2n-1) {
                background: #ececec;
            }
        }
    }
}

.flex-row-center {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;

    & > *:not(:last-child) {
        margin-right: 16px;
    }
}

.filter_exclude {
    color: #989898;
}

.cookie-warning {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    background: aliceblue;
    text-align: center;
    padding: 20px;
}

header {
    font-size: 16px;
    margin-bottom: 20px;
    font-weight: bold;
}

.locale-switch {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    margin-left: auto;
    font-size: 14px;
    font-weight: normal;

    & > * {
        cursor: pointer;

        &:not(:last-child) {
            margin-right: 10px;
        }

        &.selected {
            text-decoration: underline;
        }
    }
}